*
{
    padding: 0;
    margin: 0;
}
body
{
    background: url(https://th.bing.com/th/id/OIP.nNV2kfvntSB8M5LF86qEjQHaFN?rs=1&pid=ImgDetMain);
    background-size: cover;
    background-repeat: no-repeat;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-family: sans-serif, serif;
}
.heading
{
    font-size: 35px;
    font-weight: bold;
    text-align:center;
    margin-bottom: 40px;
}
.main-contaner
{
    background-color: rgb(0,0,0,0.4);
    border-radius: 20px;
    padding: 30px;
    margin: -30px;
}
.main-box
{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.count-section
{
    font-size: 20px;
    margin-bottom: 20px;
}
.count
{
    color: red;
    font-size: 24px;
    font-weight: 300;
}
.question-section
{
    color:black;
    font-size: 25px;
    font-weight: bold;
    text-align: left;
    margin-bottom: 30px;
}
.answer-section
{
    display: grid;
    grid-template-columns: auto auto;
    gap: 20px;
}
button
{
    font-size: 20px;
    font-weight: bold;
    padding: 20px;
    border-radius: 20px;
    background: url(https://th.bing.com/th/id/OIP.nNV2kfvntSB8M5LF86qEjQHaFN?rs=1&pid=ImgDetMain);
    background-size: cover;
    background-repeat: no-repeat;
    cursor: pointer;
}
button:hover
{
    transform: scale(0.95);
    transition: 1s;
}
button:active
{
    background: orangered;
}
.show-score
{
    background-color: white;
    border-radius: 20px;
    padding: 30px;
    margin: -30px;
    font-size: 30px;
    font-weight: 500;
    line-height: 70px;
    display: flex;
    align-items: center;
    flex-direction: column;
}
.Correct-Answers
{
    font-size: 30px;
    color: green;
}
.Wrong-Answers
{
    font-size: 30px;
    color: red;
}
.Medium
{
    font-size: 30px;
    color: orangered;
}
.verygood
{
    font-size: 30px;
    color: blue;
}